<template>
    <div>
        <article class="passJoinBox certificationBox">
            <div class="topBox">
                <h1 class="title">
                    Pass by U+ <span>금융사기방지 가입</span>
                </h1>
                <div class="con1">
                    <p class="text1">SK planet DAN & LG U+ 제휴</p>
                    <p class="text2">
                        PASS
                        <span>금융사기방지</span>로
                        <br />나의 금융거래를
                        <span>
                        더 안전하게<font>!</font>
                        </span>
                    </p>
                </div><!-- con1 end -->
				<!--====================================================== step 1 ===========================================================-->
				<div class="step1" v-if="step == 1">
					<div class="sec2">
						<p class="remark1">
							* 서비스 이용시 휴대폰인증이 필요합니다.
						</p>
						<h1 class="infoTitle">성명</h1>
						<div class="inputWrap ginputWrap">
							<input type="text"  maxlength="30" placeholder="예) 홍길동" v-model="userName">
							<ul class="cb genderUl">
								<li class="checkBox" :class="{ on : man }" @click="checkGender(1)">남</li>
								<li class="checkBox" :class="{ on : woman }" @click="checkGender(2)">여</li>
							</ul>
						</div>
						<h1 class="infoTitle">생년월일</h1>
						<div class="inputWrap binputWrap">
							<input type="text"  maxlength="8" placeholder="예) 19800101" v-model="userBirthday">
							<ul class="cb nationalityUl">
								<li><span class="" :class="{ on : local }" @click="localCheck()"></span>내국인</li>
							</ul>
						</div>
					</div>
					<div class="sec3">
						<article class="checkBoxWrap">
							<div class="iWrap1" @click="agreeAllOn()">
								<span class="checkBox" :class="{ on : agreeAll }"></span><strong>모두 동의</strong>
							</div>
							<ul class="cb checkBoxWrap2">
								<li class="iWrap2">
									<span class="checkBox" :class="{ on : agree1 }" @click="checkClick(1)"></span><strong @click="AgreeShow1()">개인정보 수집/이용 동의</strong>
								</li>
								<li class="iWrap2">
									<span class="checkBox" :class="{ on : agree2 }" @click="checkClick(2)"></span><strong @click="AgreeShow()">서비스 이용약관 동의</strong>
								</li>
								<li class="iWrap2">
									<span class="checkBox" :class="{ on : agree3 }" @click="checkClick(3)"></span><strong @click="AgreeShow3()">고유식별정보처리 동의</strong>
								</li>
								<li class="iWrap2">
									<span class="checkBox" :class="{ on : agree4 }" @click="checkClick(4)"></span><strong @click="AgreeShow4()">통신사 이용약관 동의</strong>
								</li>
							</ul>
						</article>
					</div><!-- con3 end -->
				</div>
				<!--====================================================== step 1 end ===========================================================-->

				<!--====================================================== step 2 ===============================================================-->
				<div class="step2" v-if="step == 2">
					<div class="sec2">
						<p class="remark1">
							* 서비스 이용시 휴대폰인증이 필요합니다.
						</p>
						<div class="inputWrap binputWrap">
							<input type="text" placeholder="인증번호 입력" v-model="keyNumber" maxlength="6" >
							<div class="timer">
								<span>{{ timeValue}}</span>
							</div>
						</div>
						<span class="resubmit" @click="reSubmit()">재전송</span>
					</div>
				</div>
				<!--====================================================== step 2 end ===========================================================-->
                <article class="cFooter cFooter2">
                    <div class="btnWrap btnWrap2 cb">
                        <div class="left" v-if="step == 1" @click="backMove()">이전</div>
						<div class="left left2" v-if="step == 2" @click="back(),step = 1">이전</div>
                        <div class="right" @click="checkForm1()" v-if="step == 1">확인</div>
						<div class="right right2" v-if="step == 2" >확인</div>
                    </div>
                    <p class="ex">
                        * 본 서비스는 PASS앱 (무료)을 설치한 LGU+ 고객만 이용할 수 있습니다. <br>
                        * 6개월 미사용 시 비과금됩니다. <br>
                        * 문의 : 핀브릿지 고객센터 (1688-4102) <br>
                        * 서비스 해지 : <a href="https://epass.finbridge.co.kr:39443/ew/lgCancel">https://epass.finbridge.co.kr/ew/lgCance</a>
                    </p>
                </article>
            </div>
			<!-- 개인정보 수집/이용 동의 -->
			<Agree1 :agreeShow="agreePop1" v-on:agreeCancel1="agreeCancel1"></Agree1>

			<!-- 서비스 이용약관동의 -->
			<Agree2 :agreeShow="agreePop2" v-on:agreeCancel="agreeCancel"></Agree2>

			<!-- 고유식별정보처리 동의 -->
			<Agree3 :agreeShow="agreePop3" v-on:agreeCancel3="agreeCancel3"></Agree3>

			<!-- 통신사 이용약관 동의 -->
			<Agree4 :agreeShow="agreePop4" v-on:agreeCancel4="agreeCancel4"></Agree4>
        </article>
    </div>
</template>

<script>
import Agree1 from '@/components/agree/Agree1.vue';
import Agree2 from '@/components/agree/Agree2.vue';
import Agree3 from '@/components/agree/Agree3.vue';
import Agree4 from '@/components/agree/Agree4.vue';
export default {
	components: {
		Agree1,
		Agree2,
		Agree3,
		Agree4,
	},
	data() {
		return {
			//이용약관 true로 바뀌면 밑에서 위로 올라감
			agreePop1: false,
			agreePop2: false,
			agreePop3: false,
			agreePop4: false,

			// input 휴대폰 번호값
			userPhone: '',

			// input 유저이름
			userName: '',

			// input 유저생일
			userBirthday: '',

			// checkBox 성별체크
			man: true,
			woman: false,

			// checkBox 내국인 체크
			local: true,

			// 모두 동의
			agreeAll: false,

			// 약관동의 (순서별로)
			agree1: false,
			agree2: false,
			agree3: false,
			agree4: false,

			// step1(성명,성별,생년월일,내국인 체크) , step2()
			step: 1,

			// 타이머 변수
			keyNumber: '',
			timeValue: '3:00',
		};
	},

	watch: {
		//한글 입력만 먹히도록 하는 코드 (성명입력칸에서 숫자와 영어입력이 감지될때 replace를써서 ''으로 치환)
		userName() {
			return (this.userName = this.userName
				.replace(/[a-z]/gi, '')
				.replace(/[0-9]/gi, ''));
		},
		//숫자 입력만 먹히도록 하는 코드
		userBirthday() {
			return (this.userBirthday = this.userBirthday.replace(/[^0-9]/gi, ''));
		},
	},
	methods: {
		// 개인정보 수집/이용 동의
		AgreeShow1() {
			this.agreePop1 = true;
		},
		agreeCancel1() {
			this.agreePop1 = false;
		},

		// 서비스 이용약관동의
		AgreeShow() {
			this.agreePop2 = true;
		},
		agreeCancel() {
			this.agreePop2 = false;
		},

		// 고유식별정보처리 동의
		AgreeShow3() {
			this.agreePop3 = true;
		},
		agreeCancel3() {
			this.agreePop3 = false;
		},

		// 통신사 이용약관 동의
		AgreeShow4() {
			this.agreePop4 = true;
		},
		agreeCancel4() {
			this.agreePop4 = false;
		},

		//성별체크
		checkGender(index) {
			switch (index) {
				// "남"을체크하면 "남"체크,"여"체크해제
				case 1:
					this.man = true;
					this.woman = false;
					break;

				// "여"를체크하면 "여"체크,"남"체크해제
				case 2:
					this.woman = true;
					this.man = false;
					break;
			}
		},
		// 내국인 체크 (기본적으로 체크된상태 local:true )
		localCheck() {
			this.local = !this.local;
		},

		// 모두동의
		agreeAllOn() {
			this.agreeAll = !this.agreeAll;
			if (this.agreeAll == true) {
				this.agree1 = true;
				this.agree2 = true;
				this.agree3 = true;
				this.agree4 = true;
			} else {
				this.agree1 = false;
				this.agree2 = false;
				this.agree3 = false;
				this.agree4 = false;
			}
		},

		// 이용약관체크
		checkClick(index) {
			switch (index) {
				case 1:
					this.agree1 = !this.agree1;
					break;
				case 2:
					this.agree2 = !this.agree2;
					break;
				case 3:
					this.agree3 = !this.agree3;
					break;
				case 4:
					this.agree4 = !this.agree4;
					break;
			}

			// 이용약관을 모두 체크하면 모두동의에 체크
			if (this.agree1 & this.agree2 & this.agree3 & this.agree4) {
				this.agreeAll = true;
			} else {
				this.agreeAll = false;
			}
		},

		// 휴대폰인증 타이머
		startTimer() {
			this.keyNumber = '';

			//타이머 시간
			this.timeValue = '3:00';
			//3분 => 60*3
			var timer = 180,
				minutes,
				seconds;
			var vd = this;
			this.porling = setInterval(function() {
				minutes = parseInt(timer / 60, 10);
				seconds = parseInt(timer % 60, 10);

				minutes = minutes < 10 ? minutes : minutes;
				seconds = seconds < 10 ? '0' + seconds : seconds;

				vd.timeValue = minutes + ':' + seconds;

				if (--timer < 0) {
					timer = 0;
				}
			}, 1000);
		},

		// step1 확인버튼 클릭시 step2로 넘어감
		checkForm1() {
			// 성명체크
			if (this.userName == '') {
				alert('본인의 성명이 정확히 입력하셨는지 다시 한번 확인부탁드립니다.');
				return false;
			}

			// 생년월일 체크
			if (this.userBirthday.length < 8) {
				alert(
					'본인의 생년월일을 정확히 입력하셨는지 다시 한번 확인부탁드립니다.',
				);
				return false;
			}

			// 필수약관을 동의하지 않았을때 경고문
			if (this.agree1 && this.agree2 && this.agree3 && this.agree4) {
				/* eslint-disable no-console */  
				console.log('sucsess');
			} else {
				alert('서비스 이용약관 등을 모두 동의하셔야 서비스 가입이 가능합니다.');
				return false;
			}
			this.step = 2;

			//step 1 확인버튼 클릭시 startTimer함수 실행
			this.startTimer();
		},
		backMove() {
			this.$router.replace('/');
		},

		//back이벤트 작동시 타이머 작동 초기화
		back() {
			clearInterval(this.porling);
		},

		// 재전송 버튼
		reSubmit() {
			// 재전송 버튼 클릭시 타이머 작동 초기화
			clearInterval(this.porling);
			this.startTimer();
		},
	},
};
</script>

<style>
