.certificationBox .topBox .sec3 ul.checkBoxWrap2 .iWrap2 .checkBox.on{background-repeat: no-repeat;background-position: center;background-size:cover;background-image: url('../images/pjoinCheck.png');border: none;}
.certificationBox .topBox .sec3 ul.checkBoxWrap2 .iWrap2 strong{display: inline-block;font-weight: normal;font-size: 16px;line-height: 19px;vertical-align: top;color: #999;margin-left: 10px;}

.cFooter{padding-left: 18px;padding-right: 18px;background-color: #f9f9f9;}
